import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class Nav extends Component {
	render(){
	  return(
	  	<div id="Nav">
	  	    <Link to='/react-instafeed/' id="grid-icon"><img src="data:image/png;base64," alt="View Posts as a Grid"/></Link>
	  	    <Link to='/react-instafeed/list' id="list-icon"><img src="data:image/png;base64," alt="View Posts as a List"/></Link>
	  	</div>
	  )
	}

}

export default Nav;



